.contactPage{
    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

}

.contactPageTitle{
    font-size: 3rem;
    margin-bottom: 1rem;
}
.contactDesc{
    font-weight: 300;
    font-size: 1rem;
}
.contactImgs{
    margin: 1rem 0;
    display: flex;


}

.contactImg{
    object-fit: cover;
    width: 23%;
    min-width: 9rem;
    margin: 0.2rem;
    border: 1px solid white;
    padding: 0.25rem;
    flex-wrap: wrap;
    transition: 0.3s ease;
}

.contactImg:hover{
    transform: scale(1.05);
    filter: grayscale(0%);
}

#contact{
    padding: 2rem;
}

