#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 0rem;
    margin-bottom: 1.5rem;
}
.skillDesc{
    font-weight: 300;
    font-size: 1rem;
    max-width: 51rem;
    padding: 0 2rem;
}

.skillBars{
    margin: 1rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;

}

.skillBar{
    display: flex;
    margin: 1.3rem;
    padding: 1.5rem;
    border-radius: 2rem;
    background: rgb(51, 51, 51);
}

.skillBarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 1.8rem;
}

.skillBarText>p{
    font-size: 0.9rem;
    font-weight: 200;

}

@media screen and (max-width:720px) {
    .skillBarText>p{
        font-size: 2vw;
    }

}

@media screen and (max-width:480px) {
    .skillBarText>p{
        font-size: 3vw;
    }
    
    .skillBarImg{
        height: 2.25rem;
        width: 2.25rem;
    }
    .skillBarText>h2{
        font-size: 5vw;
    }
}