

.clientForm{
    margin: 1rem ;
    margin-left: 6rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;

}

.clientPageTitle{
    font-size: 3rem;
    margin-bottom: 2rem;
    padding-right: 2rem;
    display: flex;
    text-align: center;
    justify-self: center;
    
}
.name, .email, .msg{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background: rgb(40, 40, 40);
}

.submitBtn{
    background: white;
    border: none;
    border-radius: 0.5rem;
    margin: 2rem;
    padding: 0.75rem 3.5rem;
}

.links{
    display: flex;
    flex-wrap: wrap;

}

.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;
}
#client{
    padding: -2rem;
    align-items: center;
    justify-content: center;
    margin: 7rem;
}


@media screen and (max-width:1120px) {
    .name, .email, .msg{
        font-size: medium;
        width: 100%;
        max-width: 40rem;
        margin: 0.5rem;
        padding: 0.5rem 1rem;
        justify-items: left;
    }   

    .clientForm{
        margin: 1rem;
        margin-left: -5.5rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 90vw;
        max-width: 60rem;

    }
}