#works{
    margin: 0 auto;
    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;

}

.worksTitle
{
    margin: 1rem 0;
    font-size: 3rem;
}

.worksDesc
{
    font-weight: 300;
    font-size: 0.9rem;
    max-width: 45rem;
    margin: 1rem;

}

.worksImgs{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;

}

.worksImg{
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
    transition: 0.3s ease;
}
.worksImg:hover{
    transform: scale(1.05);
    filter: grayscale(0%);
}
.workBtn{
    margin: 3rem 0;
    padding: 0.5rem 2.5rem;
    border: none;
    border-radius: 2rem;
    background: white;
    font-size: 1rem;

}
h1{
    font-size: medium;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;

}


@media screen and (max-width:585px) {
    .worksImg{
        height: 50vw;
    }
    
}

